import articleList from '@/components/articleList.vue';
export default {
	data() {
		return {
			zizhi: [],
			type: 1,
			city: '郑州市',
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	components: { articleList },
	onLoad() {

		this.city = uni.getStorageSync('CITY');
		this.getList();
		this.$Fetch({
			url: 'picture/getData',
			data: { pageNum: 1, pageSize: 10 },
			success: res => {
				console.log(res);
				this.zizhi = res.lists;
			}
		});
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'news/recommend',
					data: {
						pageNum: this.pageNum,
						pageSize: this.pageSize
					},
					debug: true,
					success: res => {
						let arr = res.lists;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);
							if (arr.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}
					}
				});
			}
		}
	}
};
